::before,
::after {
	padding: 0;
	margin: 0;
	outline: none;
}

body {
	min-height: 100vh;
	background-color: rgba(128, 128, 128, 0.09);
}

.relative-position {
	position: relative;
}

.absolute-position {
	position: absolute;
}

header {
	background-color: rgba(255, 68, 0, 0.825);
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 3rem;
	/* font-weight: bold; */
	text-shadow: 2px 2px 6px rgba(168, 148, 148, 0.8);
	box-shadow: 0px 6px 3px rgba(255, 68, 0, 0.806);
}

main {
	display: grid;
	grid-template-columns: repeat(auto-fit, 355px);
	grid-auto-rows: 285px;
	gap: 20px;
	margin-left: 1.8em;
	margin-top: 1.2em;
}

main > div {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	position: relative;
	border: 1px solid black;
	border-radius: 1.5em;
	background-color: rgb(250, 250, 210);
	padding: 0.5em 0 0.8em 0;
}

.edit-icon-div {
	width: 15px;
	right: 30px;
	top: 9px;
}

.del-icon-div {
	width: 17px;
	right: 10px;
}

.edit-icon {
	width: 15px;
	height: auto;
	cursor: pointer;
}

.delete-icon {
	width: 17px;
	height: auto;
	cursor: pointer;
}

main > div:first-child {
	grid-area: 1 / 1 / 2 / 2;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 1em;
	font-weight: bold;
}

#plus {
	cursor: pointer;
	width: 100px;
	height: auto;
}

dialog:has(.new-project-input-dialog) {
	width: 350px;
	height: 140px;
	position: absolute;
	top: 10vh;
	right: 0;
	left: 0;
	margin: auto;
	border: 1px solid black;
}

.new-project-input-dialog {
	width: 100%;
	height: 100%;
	background-color: rgb(232, 230, 230);
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 0.8em;
}

#close-new-project-input-dialog-btn,
#close-todo-popup-form-btn,
#close-checklist-btn {
	width: 15px;
	height: auto;
	align-self: end;
	border: 1px solid black;
	border-top-style: none;
	border-right-style: none;
	border-radius: 0.1em;
	cursor: pointer;
}

#add-button {
	width: 60px;
	cursor: pointer;
}

dialog:has(.todo-popup-form) {
	width: 430px;
	height: 485px;
	border-radius: 0.3em;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	border: 1px solid black;
}

.todo-popup-form {
	width: 410px;
	height: 100%;
	display: flex;
	flex-direction: column;
	gap: 1em;
	background-color: rgb(238, 236, 236);
	padding: 0 0 0 20px;
}

.todo-popup-form label:first-child,
#status-label {
	display: inline-block;
	width: 80px;
	text-align: right;
	vertical-align: top;
}

.status-options {
	text-align: right;
	vertical-align: top;
}

input,
textarea,
select {
	outline: none;
}

textarea {
	font-family: 'Arial';
}

#todo-form-project-name {
	align-self: center;
	margin-bottom: 0.5em;
	font-weight: bold;
}

#checklist-btn {
	align-self: start;
	padding: 0.1em 0.7em;
	margin-left: 6.4em;
	background-color: rgb(229, 228, 228);
	cursor: pointer;
}

#checklist-btn:hover {
	background-color: rgb(250, 250, 9);
}

#todo-save-btn {
	align-self: center;
	padding: 0.1em 0.7em;
	cursor: pointer;
}

.project-name {
	font-weight: bold;
	text-align: center;
	font-size: 1.3rem;
	outline: none;
}

.todo-list {
	padding: 0 1em 1em 1em;
	height: 60%;
	display: flex;
	flex-direction: column;
	gap: 0.8em;
	overflow-y: auto;
}

.new-todo-btn {
	width: 50%;
	align-self: center;
	padding: 0.5em 0;
	border-radius: 0.5em;
	cursor: pointer;
	outline: none;
}

.new-todo-btn:hover {
	background-color: rgb(255, 140, 0);
}

.lightgraybg {
	background-color: lightgray;
}

.todo {
	display: grid;
	grid-template-columns: 1.7fr 0.9fr 0.8fr;
	grid-template-rows: 1fr 1fr;
	height: 25%;
	gap: 0.3em;
	flex-shrink: 0;
}

.todo-name {
	width: 85%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.priority-and-status {
	border: 1px solid black;
	border-radius: 0.4em;
	text-align: center;
	font-size: 0.95rem;
	cursor: default;
}

.edit-and-delete {
	border: 1px solid black;
	border-radius: 0.4em;
	cursor: pointer;
}

.edit-and-delete:hover {
	background-color: rgb(228, 225, 225);
}

.lightgreenbg {
	background-color: lightgreen;
}

.yellowbg {
	background-color: yellow;
}

.redbg {
	background-color: red;
}

dialog:has(.confirmation-dialog) {
	width: 350px;
	height: 80px;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	border: 1px solid black;
	overflow: clip;
}

#confirmation-text {
	text-align: center;
	margin-bottom: 8px;
}

.confirmation-dialog {
	background-color: rgb(222, 220, 220);
	height: 100%;
	padding-top: 20px;
}

.yesnobuttonsdiv {
	display: flex;
	justify-content: center;
	gap: 1em;
}

#yes-button,
#no-button {
	cursor: pointer;
	outline: none;
	width: 10%;
}

.tooltip-regular {
	background-color: rgb(236, 234, 234);
	font-size: 14px;
	left: 15px;
	bottom: 15px;
	z-index: 1;
}

.tooltip-rightmost {
	background-color: rgb(236, 234, 234);
	font-size: 14px;
	right: 15px;
	bottom: 15px;
	z-index: 1;
}

.hidden {
	visibility: hidden;
}

dialog:has(.checklist) {
	width: 430px;
	height: 485px;
	border-radius: 0.3em;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	border: 1px solid black;
}

.checklist {
	width: 430px;
	height: 100%;
	display: flex;
	flex-direction: column;
	gap: 1.5em;
	background-color: rgb(214, 211, 211);
	overflow-y: auto;
}

#checklist-heading {
	align-self: center;
	font-weight: bold;
	border-bottom: 1px solid black;
	padding-bottom: 0.05em;
	letter-spacing: 0.04em;
}

.checklist-bottom-buttons-div {
	display: flex;
	gap: 1em;
	align-self: center;
	margin-top: 1em;
	margin-bottom: 1em;
}

#add-new-item-btn,
#done-checklist-btn,
#save-new-checklist-item-btn {
	width: 8em;
	border: 1px solid black;
	border-radius: 0.4em;
	padding: 0.2em 0;
	background-color: rgb(232, 230, 230);
	outline: none;
	cursor: pointer;
}

#add-new-item-btn:hover,
#done-checklist-btn:hover,
#save-new-checklist-item-btn:hover {
	background-color: rgb(222, 219, 219);
}

.checklist-items-div {
	display: flex;
	flex-direction: column;
	gap: 2em;
}

.checklist-item-div {
	display: grid;
	grid-template-columns: 5fr 1.6fr 1fr 1fr;
	margin-left: 1.8em;
}

.input-field-div-for-new-item {
	display: flex;
	justify-content: center;
	gap: 2em;
}

.display-none {
	display: none;
}
